import React from 'react'
import Logo from '../../img/icons8-google-play-movies-and-tv-48.png'
import './Navbar.css'

function Navbar(){
      return(
          <div className="navbar">
              <div className='content'>
              <div className="navbar_ico">
                  <img src={Logo}/>
              </div>
              <ul className="navbar_list">
                  <li className="navbar_item">Home</li>
                  <li className="navbar_item">Films</li>
                  <li className="navbar_item">MultFilms</li>
                  <li className="navbar_item">TV-channel</li>
              </ul>
              </div>
          </div>
      )
 }


export default Navbar;